.baseline{
    text-align: center;
    font-weight: bold;
    position:relative;
    margin:30px auto 25px; 
    @include rwd(phone){
        margin:20px auto;
    }
    span{
        font-weight: 300;
        font-size:$fs-mid;
        line-height: 30px;
        color:#666;
        background-color: #fff;
        padding:0 25px;
        letter-spacing:3px;
    }
    &:after{
        content:'';
        position:absolute;
        width: 100%;
        height: 1px;
        background-color: #EFEFEF;
        left: 0;
        top:50%;
        z-index: -1;
    }
    &2{
        font-weight: 300;
        text-align: center;
        font-weight: bold;
        position:relative;
        margin:30px auto;
        @include rwd(phone){
            margin:20px auto;
        }

        span{
            font-weight: 300;
            font-size:$fs-mid;
            color:black;
            background-color: #fff;
            padding:7px 25px;
            line-height: 30px;
            letter-spacing:3px;
            position:relative;
            z-index: 2;
        }
        &:after{
            content:'';
            position:absolute;
            width: 100%;
            height: 1px;
            background-color: #EFEFEF;
            left: 0;
            top:50%;
            z-index: 1;
        }
    }
}
.baseline3{
    text-align: center;
    font-weight: bold;
    margin:30px auto 60px; 
    padding-bottom: 10px;
    border-bottom:2px solid #E53A31;
    span{
        color:#E53A31;
        font-size:$fs-mid;
    }
    @include rwd(phone){
        margin:30px auto 40px;
    }
}
