/*****select drop******/
.select_drop{
    float:right;
    display:inline-block;
    .custom-select-wrapper {
        position: relative;
        display: inline-block;
        user-select: none;
        select {
            display: none;
        }
    }

    .custom-select {
        position: relative;
        display: inline-block;
    }

    .custom-select-trigger {
        position: relative;
        display: block;
        padding: 7.5px 15px 7.5px 30px;
        font-size: $fs-min;
        font-weight: 300;
        color: #868686;
        border:1px solid #EBEBEB;
        cursor: pointer;
        @include rwd(phone){
            padding: 7.5px 5px 7.5px 25px;
        }
        &:after {
            position: absolute;
            display: block;
            content: '';
            width: 10px;
            height: 10px;
            top: 50%;
            left: 12px;
            margin-top: -3px;
            border-bottom: 1px solid #868686;
            border-right: 1px solid #868686;
            transform: rotate(45deg) translateY(-50%);
            transition: all .4s ease-in-out;
            transform-origin: 50% 0;
            @include rwd(phone){
                left: 8px;
            }
        }
    }

    .custom-select.opened .custom-select-trigger:after {
        margin-top: 3px;
        transform: rotate(-135deg) translateY(-50%);
    }

    .custom-options {
        display: none;
        min-width: 100%;
        border:1px solid #EBEBEB;
        box-sizing: border-box;
        box-shadow: 0 2px 1px rgba(0, 0, 0, 0.07);
        background: #fff;
        transition: all .4s ease-in-out;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transform: translateY(-15px);
    }

    .custom-select.opened .custom-options {
        position:absolute;
        z-index: 2;
        opacity: 1;
        display:block;
        visibility: visible;
        pointer-events: all;
        transform: translateY(0);
    }
    .option-hover:before {
        background: #f9f9f9;
    }

    .custom-option {
        position: relative;
        display: block;
        padding: 10px 15px;
        border-bottom: 1px solid #EBEBEB;
        font-size: $fs-min;
        color: #868686;
        cursor: pointer;
        text-align: right;
        transition: all .4s ease-in-out;
        &:hover{
            color:$pri_red;
        }
        &:first-of-type {
            border-radius: 4px 4px 0 0;
        }
        &:last-of-type {
            border-bottom: 0;
            border-radius: 0 0 4px 4px;
        }
        &:hover, &.selection {
            background: #f9f9f9;
        }
    }
}


//custom checkbox

input.customCB[type=checkbox] { 
    display:none; 
    &+label:before {
        font-family: FontAwesome;
        display: inline-block;
        content: "\f096";color:#aaa;
        letter-spacing: 10px;
    }
    &:checked + label:before { 
        content: "\f046";color:#495057; 
        letter-spacing: 5px;
    }
} 


