//md
Tablets (≥768px - <992px) Large devices
// lg,xl
Desktops (≥992px - <1200px) Extra large devices
Desktops (≥1200px)
*/

@import url('https://fonts.googleapis.com/css?family=Lobster+Two');

* {
    font-size: 16px;
    letter-spacing: 2px;
    line-height: normal;
    box-sizing:border-box;
    @media(max-width:576px){
        font-size: 14px;
        letter-spacing: 1px;
    }
