    //     border-radius: 10px;
    //     background: #888;
    // }
    // &::-webkit-scrollbar-thumb:window-inactive {
    //     background: rgba(100, 100, 100, 0.4);
    // }
}

body {
    background-color: transparent;
}
.container{
    @media(max-width:1199px){
        min-width: calc(100% - 30px);
    }
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
header,
font,
article {
    word-wrap: break-word; // word-break: break-all;
    word-break: normal;
    &.grey {
        color: #666;
    }
}
.overflow-hidden{
    overflow:hidden;
}
.leave-top{
    margin-top: 79.5px;
    @media(max-width:991px){
        margin-top: 67.5px;
    }
}
.leave-bot{
    margin-bottom: 100px;
    @media(max-width:991px){
        margin-bottom: 80px;
    }
}
.prl-15{
    padding-left: 15px;
    padding-right: 15px;
}
.grey{
    color:#666;
}

.fullScreen{
    min-height: 100vh;
    position: relative;
    padding:10vh 0px;
}
.halfScreen{
    min-height: 50vh;
    position: relative;
    padding:10vh 15px;
}
.lobster-font{
    font-family: 'Lobster Two', cursive;
    &.font40{
        font-size: 40px;
        margin-bottom: 60px;
        @include rwd(phone){
            font-size: 36px;
            margin-bottom: 30px;
        }
    }
}
footer{
    background-color: RGBA(80, 111, 81, 1.00);
    padding: 30px 0;
    @include rwd(phone){
        padding: 40px !important;
    }
    a{
        margin-bottom: 10px;
        display: block;
        color:white;
        font-size: 13px;
        .svg-farrow{
            width: 20px;
            margin-right: 10px;
        }
        &:hover{
            opacity:.7;
            color:white;
        }
    }
    h3{
        font-size: 20px;
        letter-spacing: 1.5px;
        margin-bottom: 30px;
        color:white;
    }
    p{
        font-size: 14px;
        letter-spacing: 1px;
        margin-bottom: 5px;
        color:white;
    }
    .footer-link{

        @include rwd(phone){
           
            margin-top: 40px;
        }
        
    }
    .footer-social{
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        margin-bottom: 5px;
        @include rwd(phone){
            margin-top: 20px;
            margin-bottom: 0;
            align-items: center;
            justify-content: center;
        }
        ul{
            margin-bottom: 0;
            li{
                display: inline-block;
                margin: 0 8px;
                a{
                    font-size: 28px;
                    margin-bottom: 0;

                }
            }
        }
    }
}
.indexBn-box{
    position: relative;
    margin-bottom: 100px;
    @include rwd(phone){
        margin-bottom: 40px;
    }

    .indexBn-slick{
        width: 100%;
        display: block;
        margin-bottom: 0;
        li.bn{
            height: 80vh;
            background-size: cover;
            @include rwd(phone){
                height: 60vw;
            }
        }
    }
    .arrow-control{
        .right,.left{
            position: absolute;
            top: 50%;
            transform:translateY(-50%);
        }
        .right{
            right: 3%;
        }
        .left{
            left: 3%;
        }
        img{
            width: 30px;
        }
    }
    .slick-dots{
       
        li {
            &.slick-active{
                button{
                    &:before{
                        color:#666;
                    }
                }
            }
            button{
                &:before{
                    color:#999;
                    font-size: 14px;
                }
            }
        }
        bottom: 10px;
        @include rwd(phone){
            bottom: -25px;
            z-index: 20;
            button{
                &:before{
                    font-size: 12px;
                }
            }
        }
    }
}
%font30{
    font-size: 30px;
    letter-spacing: 2px;
    @include rwd(phone){
        font-size: 24px;
        letter-spacing: 1px;
    }
}
%font20{
    font-size: 20px;
    letter-spacing: 1.5px;
    @include rwd(phone){
        font-size: 18px;
        letter-spacing: .8px;
    }
}
%font16{
    font-size: 16px;
    letter-spacing: 1.3px;
    @include rwd(phone){
        font-size: 14px;
        letter-spacing: .4px;
    }
}
.ip-box{
    box-shadow: 0px 0px 10px 5px #eaeaea;
    padding: 20px;
    margin-top: 15px;
    margin-bottom: 15px;
    @include rwd(phone){
        padding: 20px 10px;
    }
    figure{
        position: relative;
        figcaption{
            position: absolute;
            top: -20px;
            left: -20px;
            width: 100px;
            height: 100px;
            @include rwd(phone){
                left: -10px;
            }
            p{
                position: absolute;
                width: 100%;
                height: 100%;
                color:white;
                z-index: 3;
                top: 8px;
                left: 10px;
            }
            span{
               position: absolute;
               z-index: 1;
               top: 0;
               left: 0;
               width: 100%; 
               width: 0;
               height: 0;
               border-style: solid;
               border-width: 100px 100px 0 0;
               border-color: RGBA(255, 149, 150, 1.00) transparent transparent transparent;


            }
        }
    }
    .ip-name{
        @extend %font20;
        margin-bottom: 10px;
        margin-top: 15px;
        text-align: center;

    }
    .ip-price-onsale{
        font-size: 20px;
        letter-spacing: 1.4px;
        text-align: center;
        margin-bottom: 0px;
        color:$pri_red;
        @include rwd(phone){
            font-size: 16px;
            letter-spacing: .6px;
        }
    }
    .ip-price{
        @extend %font16;
        margin-bottom: 15px;
        color:#666;
        text-align: center;
        display: block;

    }
    .ip-cart{
        display: block;
        margin: auto;
        letter-spacing: 2px;
        border-radius:5px;
        padding: 10px 0;
        text-align: center;
        max-width:200px;
        width: 90%;
        background-color: RGBA(89, 143, 93, 1.00);
        color:white;
        &:hover{
            background-color: #000;
            color:white;
        }
    }
}
.index-bn2{
    background-size: cover;
    background-position: center center;
    min-height:60vh;
    position: relative;

    padding-top: 20px;
    padding-bottom: 20px;
    .bn-text1,.bn-text2{
        background-color: rgba(255,255,255,.7);
        margin-top: 5%;
        margin-bottom: 5%;
        padding: 2% 3%;
        width:auto;
        @media(max-width:768px){
            background-color: rgba(255,255,255,.85);
            width: 100%;
            position: absolute;
            bottom: 0;
            left: 0;
            min-height: 120px;
            margin-top: 0;
            margin-bottom: 20px;
        }
        h2{
            font-size: 40px;
            color:RGBA(73, 136, 80, 1.00);
            font-weight: 400;
            margin-bottom: 20px;
            @media(max-width:768px){
                font-size: 20px;
                letter-spacing: 2px;
                margin-bottom: 15px;
            }
        }
        p{
            font-size: 30px;
            font-weight: 400;
            color: #000;
            margin-bottom: 0;
            @media(max-width:768px){
                font-size: 15px;
                letter-spacing: 1px;
            }
        }
    }
    .bn-text2{
        @include rwd(ex_phone){
            position: absolute;
            right: 0;
            top: 50%;
            transform:translateY(-50%);
            margin-top: 0;
            margin-bottom: 0;

        }
    }
    .bn3-box{
        padding:5%;
        h2{
            margin-bottom: 30px;
            font-size: 30px;
            letter-spacing: 3px;
            @include rwd(phone){
                text-align: center;
            }
        }
        p{
            font-size: 16px;
            line-height: 30px;
            letter-spacing: 2px;
            text-align: justify;
        }
    }

}
.index-p1-out,.index-p2-out,.index-p3-out{
    position: relative;
    .index-control{
        .left,.right{
            svg{
                position: absolute;
                width:40px;
                top: 50%;
                transform:translateY(-50%);
                color:#333;
                &:hover{
                    fill: grey;
                }
                @include rwd(phone){
                    width:20px;
                    
                }
            }
        }
        .left{
            svg{
                left: -25px;
                @include rwd(phone){
                    left: -10px;
                }
            }
        }
        .right{
            svg{
                right:-25px;
                @include rwd(phone){
                    right: -10px;
                }
            }
        }
    }
}
.seemore{
    text-align: center;
    padding:12px 0px;
    margin: 30px auto 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    border:1px solid #000;
    transition:.3s ease-in all;
    color: #000;
    svg{
        width: 20px;
        margin-right: 10px;
        *{
            transition:.3s ease-in all;
        }
    }
    &:hover{
        border-color:transparent;
        background-color: #000;
        color: #fff;
        svg{
            circle{
                fill:#fff;
            }
            path{
                fill:#000;
            }
        }
    }
}
.index-adslide{
    position: relative;
    .index-adslide-ul{
        position: relative;
        width: 100%;
        float: left;
        display: block;
        figure{
            margin-bottom: 0;
        }
    }
    .index-control{
        .left,.right{
            svg{
                position: absolute;
                width:40px;
                top: 50%;
                transform:translateY(-50%);
                color:#333;
                &:hover{
                    fill: grey;
                }
                @include rwd(phone){
                    width:20px;
                    
                }
            }
        }
        .left{
            svg{
                left: 15px;
            }
        }
        .right{
            svg{
                right:15px;
            }
        }
    }
}
.index-article-box{
    background-color: RGBA(236, 237, 232, 1.00);
    padding-top: 50px;
    padding-bottom: 50px;
    .index-article{
        display: flex;
        align-items: stretch;
        margin-bottom: 30px;
        box-shadow:0px 0px 10px 3px #ccc;
        .image{
            
            background-size: cover;
            @media(max-width:991px){
                height: 60vw;
            }
        }
        .intro{
            padding: 3%;
            background-color: #fff;
            h3{
                @extend %font30;
                font-weight: 400;
                margin-bottom: 20px;
            }
            .price{
                @extend %font20;
                color:$pri_red;
                margin-bottom: 25px;
            }
            .info{
                @extend %font16;
                margin-bottom: 40px;
            }
        }

    }
    .link{
        float: right;
        display: flex;
        align-items: center;
        color:black;
        svg{
            width: 20px;
            margin-right: 10px;
            *{
                transition:.3s ease-in all;
            }
        }
        &:hover{
            border-color:transparent;
            color: #6d6d6d;
            svg{
                circle{
                    fill:#6d6d6d
                }
                
            }
        }
    }
}   